<template>
  <div class="api-detail-page">
    <div class="content-header">
      <bread-crumb
        current-level="API详情"
        upper-level="数据分享"
        back-path="/main/data-share"
      />
    </div>
    <div class="api-detail-body">
      <gs-loading :is-show="isDetailLoading" />
      <div class="api-base-info">
        <div class="info-text">
          <div class="api-name">
            {{ detailData.name || '-' }}
            <span
              :class="{'status-label': true, 'status-published': detailData.publish}"
            >{{ detailData.publish ? '已发布' : '未发布' }}</span>
          </div>
          <div class="info-item">
            <div class="info-item-text">
              创建人：{{ detailData.keeper || '-' }}
            </div>
            <div class="info-item-text">
              创建时间：{{ detailData.generateTime || '-' }}
            </div>
            <div class="info-item-text">
              修改人：{{ detailData.updater || '-' }}
            </div>
            <div class="info-item-text">
              修改时间：{{ detailData.updateTime || '-' }}
            </div>
          </div>
        </div>
        <div class="action-group">
          <gs-button
            v-if="detailData.edit && !detailData.publish"
            type="text-primary"
            @click="publish"
          >发布</gs-button>
          <gs-button
            v-if="detailData.edit && detailData.publish"
            type="text-primary"
            @click="unPublish"
          >撤销发布</gs-button>
          <gs-button
            type="text-primary"
            @click="copyApi"
          >克隆</gs-button>
          <gs-button
            v-if="detailData.edit"
            :disabled="detailData.publish"
            :title="detailData.publish ?'如欲编辑，请先撤销发布该API' : ''"
            type="text-primary"
            @click="editApi"
          >编辑</gs-button>
          <gs-button
            v-if="detailData.edit"
            :disabled="detailData.publish"
            :title="detailData.publish ?'如欲删除，请先撤销发布该API' : ''"
            type="text-primary"
            @click="deleteApi"
          >删除</gs-button>
        </div>
      </div>
      <div class="api-extract-info">
        <gs-tabs
          v-model="activeTab"
          type="content-tab"
        >
          <gs-tab-pane
            label="API信息"
            name="info"
          >
            <div class="info-body">
              <div class="info-body-base">
                <div class="info-body-base-title">基本信息</div>
                <div class="info-body-base-item">
                  <div class="info-item-left">
                    <div class="info-item-left-text">
                      <span class="info-label">API名称：</span>{{ detailData.name || '-' }}
                    </div>
                    <div class="info-item-left-text">
                      <span class="info-label">API分组：</span>{{ detailData.groupName || '-' }}
                    </div>
                    <div class="info-item-left-text">
                      <span class="info-label">版本：</span>{{ detailData.version || '-' }}
                    </div>
                    <div class="info-item-left-text">
                      <span class="info-label">描述：</span>{{ detailData.description || '-' }}
                    </div>
                  </div>
                  <div class="info-item-right">
                    <div class="info-item-right-text">
                      <span class="info-label">协议类型：</span>{{ detailData.protocol || '-' }}
                    </div>
                    <div class="info-item-right-text">
                      <span class="info-label">请求方式：</span>{{ detailData.requestMode || '-' }}
                    </div>
                    <div class="info-item-right-text">
                      <span class="info-label">返回类型：</span>{{ detailData.returnType || '-' }}
                    </div>
                    <div class="info-item-right-text">
                      <span class="info-label">API路径：</span>{{ detailData.path || '-' }}
                    </div>
                  </div>
                </div>
              </div>
              <div class="info-body-data">
                <div class="info-body-data-title">数据信息</div>
                <div class="info-body-data-item">
                  <div class="info-item-text">
                    数据库：{{ detailData.dbName || '-' }}
                  </div>
                  <div class="info-item-text">
                    数据表：{{ detailData.tableName || '-' }}
                  </div>
                  <div class="info-item-text">
                    数据Owner：{{ dataOwners }}
                  </div>
                </div>
                <div class="info-body-data-table">
                  <el-table
                    :data="apiTableData"
                  >
                    <el-table-column
                      type="index"
                      label="序号"
                    />
                    <el-table-column
                      prop="columnName"
                      label="名称"
                    />
                    <el-table-column
                      prop="filter"
                      label="筛选字段"
                    >
                      <template slot-scope="scope">
                        {{ scope.row.filter ? '是' : '否' }}
                      </template>
                    </el-table-column>
                    <el-table-column
                      prop="fill"
                      label="必传"
                    >
                      <template slot-scope="scope">
                        {{ scope.row.fill ? '是' : '否' }}
                      </template>
                    </el-table-column>
                    <el-table-column
                      prop="defaultValue"
                      label="缺省值"
                    />
                  </el-table>
                </div>
              </div>
            </div>
          </gs-tab-pane>
          <gs-tab-pane
            label="测试"
            name="test"
          >

            <div class="info-body">
              <div class="req-msg">
                <h5 class="title">请求信息</h5>
                <div class="request">
                  <div
                    v-for="(item, index) in testData"
                    :key="index"
                    class="fields"
                  >
                    <gs-checkbox
                      v-model="item.check"
                      class="name"
                    >
                      <span
                        :title="item.columnName"
                      >{{ item.columnName }}</span>
                    </gs-checkbox>
                    <span class="value">
                      <gs-date-picker
                        v-if="item.type === 'timestamp'"
                        v-model="item.value"
                        type="date-time"
                      />
                      <gs-radio-group
                        v-else-if="item.type === 'boolean'"
                        v-model="item.value"
                      >
                        <gs-radio :label="true" />
                        <gs-radio :label="false" />
                      </gs-radio-group>
                      <gs-input-number
                        v-else-if="numberType.includes(item.type)"
                        v-model="item.value"
                      />
                      <gs-input
                        v-else
                        v-model="item.value"
                      />
                    </span>
                  </div>
                  <div class="fields">
                    <span class="name">limit</span>
                    <span class="value">
                      <gs-input-number
                        :precision="0"
                        v-model="limit"
                      />
                    </span>
                  </div>
                  <div class="fields">
                    <span class="name">offset</span>
                    <span class="value">
                      <gs-input-number
                        :precision="0"
                        v-model="offset"
                      />
                    </span>
                  </div>
                  <div class="gap" />
                </div>
                <div class="test-operation">
                  <gs-button
                    :disabled="!btnLoading"
                    @click="stopTest"
                  >停止测试</gs-button>
                  <gs-button
                    :loading="btnLoading"
                    type="primary"
                    @click="startTest"
                  >开始测试</gs-button>
                </div>
              </div>
              <div class="rep-res">
                <h5 class="title">返回数据</h5>
                <div class="response">
                  <gs-tabs
                    v-model="activeName"
                    type="content-tab"
                  >
                    <gs-tab-pane
                      label="编码模式"
                      name="json"
                    >
                      <gs-textarea
                        :value="testResStringify"
                        :autosize="{
                          minRows: 8,
                          maxRows: 12
                        }"
                        readonly
                      />
                    </gs-tab-pane>
                    <gs-tab-pane
                      label="表格模式"
                      name="table"
                    >
                      <el-table
                        :data="testRes"
                      >
                        <el-table-column
                          v-for="(item, index) in tableHeader"
                          :key="index"
                          :label="item"
                          :prop="item"
                        />
                      </el-table>
                    </gs-tab-pane>
                  </gs-tabs>
                </div>
              </div>
            </div>
          </gs-tab-pane>
        </gs-tabs>
      </div>
    </div>
  </div>
</template>

<script>
import {
  mapState,
  mapActions
} from 'vuex';
import BreadCrumb from 'src/components/breadcrumb';
import * as service from 'src/service/data-share/index';
import axios from 'axios';
import {
  checkLoading
} from 'src/store/status';
import './style';
let randomName = Math.random().toString(36).slice(-8);

export default {
  components: {
    BreadCrumb
  },

  data() {
    return {
      apiId: '',
      activeTab: 'info',
      testData: [],
      limit: 10,
      offset: 0,
      btnLoading: false,
      activeName: 'json',
      testRes: [],
      numberType: ['double', 'float', 'int', 'bigint', 'decimal']
    };
  },

  computed: {
    ...mapState('datashare', {
      apiDetail: 'apiDetail'
    }),
    detailData() {
      const {
        apiDetail
      } = this;
      return apiDetail.data || {};
    },
    apiTableData() {
      return this.detailData.fields || [];
    },
    dataOwners() {
      const owners = this.detailData.dataOwner || [];
      return owners.join('，') || '-';
    },
    isDetailLoading() {
      return checkLoading(this.apiDetail.status);
    },
    tableHeader() {
      const {testRes} = this;
      if (testRes && testRes[0]) {
        return Object.keys(testRes[0]);
      } else {
        return [];
      }
    },
    testResStringify() {
      return JSON.stringify(this.testRes, null, 4);
    }
  },

  mounted() {
    const param = this.$route.params || {};
    const id = param.id;
    this.apiId = id;
    if (id) {
      this.getApiDetail(id).then(_ => {
        this.testData = (this.detailData.fields || [])
          .filter(item => item.filter)
          .map(item => {
            return {columnName: item.columnName, value: '', check: true, type: item.type, fill: item.fill};
          });
      });
    }
  },

  methods: {
    ...mapActions('datashare', [
      'getApiDetail',
      'delApi',
      'unPublishApi'
    ]),
    deleteApi() {
      this.$Modal.confirm({
        title: '提示',
        content: '确定删除该API吗？',
        onOk: () => {
          this.delApi(this.apiId).then(res => {
            this.$Message.success('删除成功');
            this.$router.push('/main/data-share');
          });
        }
      });
    },
    publish() {
      localStorage && localStorage.setItem('SELECTED_API', JSON.stringify([this.detailData]));
      this.$router.push('/main/data-share/publish');
    },
    unPublish() {
      this.$Modal.confirm({
        title: '提示',
        content: '确定撤销发布该API吗？',
        onOk: () => {
          this.unPublishApi([this.apiId]).then(data => {
            this.$Message.success(`撤销成功`);
            this.getApiDetail(this.apiId);
          });
        }
      });
    },
    copyApi() {
      this.$router.push({
        name: `/main/data-share/create`,
        query: {
          apiId: this.apiId
        }
      });
    },
    editApi() {
      this.$router.push({
        name: `/main/data-share/create`,
        query: {
          apiId: this.apiId,
          isEdit: true
        }
      });
    },
    stopTest() {
      this.source.cancel();
      service.stopTestApi(randomName).then(data => {
        this.$Message.success(`停止成功`);
      });
    },
    startTest() {
      if (this.checkTestInput()) {
        const {
          tableGuid,
          dbGuid,
          maxRowNumber
        } = this.detailData;
        const parameter = this.testData.filter(item => item.check)
          .map(item => {
            if (item.type === 'timestamp' && item.value) {
              return {columnName: item.columnName, value: [item.value.unix()]};
            }
            return {columnName: item.columnName, value: [item.value]};
          });
        const queryFields = this.detailData.fields.map(item => item.columnName);
        const params = {
          tableGuid,
          dbGuid,
          maxRowNumber,
          parameter,
          queryFields,
          limit: this.limit,
          offset: this.offset
        };
        randomName = Math.random().toString(36).slice(-8);
        this.btnLoading = true;
        const { CancelToken } = axios;
        this.source = CancelToken.source();
        service.testApi({params, randomName}, this.source.token).then(data => {
          this.testRes = data;
          this.btnLoading = false;
        }).catch(e => {
          this.btnLoading = false;
          console.log('Request canceled', e.message);
        });
      }
    },
    checkTestInput() {
      const fill = this.testData.filter(item => item.fill && !item.check);
      if (fill && fill.length) {
        const str = fill.map(i => i.columnName).join(',');
        this.$Message.error(`${str}字段为必填字段，请选中并输入数据`);
        return false;
      }
      const checkStr = this.testData.filter(item => item.check)
        .filter(item => {
          if (
            (this.numberType.includes(item.type) && item.value === '') ||
          (item.type === 'timestamp' && item.value === '')
          ) {
            return true;
          } else {
            return false;
          }
        })
        .map(i => i.columnName).join(',');
      if (checkStr) {
        this.$Message.error(`${checkStr}字段为必填字段，请输入数据`);
        return false;
      }
      return true;
    }
  }
};
</script>



